/*------------------ General ------------------*/

.container {
  max-width: 1220px;
  margin: auto;
}

.head-banner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  height: 600px;
  background: grey;
  margin-bottom: 70px;
}

.head-banner h1 {
  padding: 35px 65px;
}

.btn {
  text-decoration: none;
  border: 1px solid black; 
  padding: 10px 10px;
  color: #000;
}

.btn:hover {
  text-decoration: none;
  background-color: #000;
  color: #fff;
}

.success-message {
  text-align: center;
  padding: 10px;
  color: #4fa94f;
  background: #98e1b4;
}

/*------------------ Header ------------------*/

header {
  display: block;
  position: fixed;
  width: 100%;
  height: 70px;
  z-index: 1000;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

header h1 {
  font-size: 2.5rem;
  -webkit-transition: all 0.7s;
  -o-transition: all 0.7s;
  transition: all 0.7s;
}

header a {
  float: left;
  padding: 17px 30px;
}

nav {
  float: right;
  padding: 30px;
}

nav a {
  padding: 0 20px;
}

.sticky{
  background: #000;
}

.sticky h1 {
  color: #fff;
  font-size: 2.1rem;
}

/*------------------ Footer ------------------*/

footer {
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: white;
  background: darkgray; 
}

footer p {
  margin: 0;
  padding-left: 30px;
}

/*------------------ Tech section ------------------*/
.tech-section {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  
}

.tech-section-text {
  max-width: 400px;
  margin:auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
}


.tech-section img {
  width: 500px;
}

/*------------------ About Page ------------------*/

.our-misssion {
  margin-top: 70px;
}

.our-misssion, .our-technology {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.our-misssion .text, .our-technology .text {
  width: 30%;
}

.video {
  margin: 70px 0;
  text-align: center;
}


/*------------------ Contact Page ------------------*/

.form-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-top: 70px;
}

.shop-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-top: 70px;
}

form {
  width: 400px;
}

.shop-section .text {
  background: #f4f3f3;
  padding-top: 100px;
  padding-left: 60px;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

#map {
  width: 400px;
  height: 400px;
}


/* ------------------ Home Page ------------------- */


.news-section-top{
height: 600px;
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-align: center;
    align-items: center;
}

.news-container{
max-width: 1260px;
}

.news-section-down{
  background-color: antiquewhite;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.news-section-down-wrapper{
  margin-top: -8%;
  margin-bottom: 6%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  
}

.brand-section{
  margin-top: 70px;
/* padding: 1%; */
/* background-color: bisque; */

}

.brand-section-wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

/*------------------ Product Detailed Page ------------------*/

.product-section {
  max-width: 800px;
  margin: auto;
}

.product-main-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin: 0 0 5%;
}

.product-main-info img {
  height: 350px;
  margin-right: 100px;
}

.product-main-info div {
  margin-top: 5%;
}

.product-main-info p {
  font-size: 25px;
}

.product-other-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 25px 0 80px; 
}

.details {
  max-width: 350px;
}

/*------------------ Product List Page ------------------*/


.filter-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0;
}

.filter-item  {
  margin-right: 30px;
  padding-right: 30px;
  border-right: #000 solid 1px;
}

.filter-item:hover, .filter-last-item:hover {
  font-weight: bold;
  cursor: pointer;
}

.filter-input {
  margin-right: 15px;
}

.dropdown-toggle::after {
  display: none;
}

.product-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 70px 0;
  padding: 0;
  list-style-type: none;
}

.product-list li {
  margin-bottom: 5%;
  position: relative;
}

.product-list-item img { 
  width: 350px;
  height: 350px;
}

.product-list-overlay {
  position:absolute;
  background-color:rgba(255, 255, 255, 0.5);
  opacity: 0;
  top: 0;
  bottom:0;
  left:0;
  right:0;
}
.product-list-overlay:hover {
  display: block;
  opacity: 1;
}




/*-------------------------- Account Page ------------------------- */

.account-section-wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  height: 600px;
}

.member-space-section{
  /* height: 600px; */
}

.log-in {
  border-left: 1px solid blue;
  padding-left: 200px;
  height: 300px;
}

.account-section-wrapper{
  margin-top: 200px;
}


